import React from "react";
import Card from "react-bootstrap/Card";
import Table from "react-bootstrap/Table";

function DisplayWeather(props) {
  const { data } = props;
  console.log(data);

  const iconlink =
    "http://openweathermap.org/img/wn/" +
    `${data.cod !== 404 ? data.weather[0].icon : null}` +
    ".png";

  return (
    <div className="displayweather">
      {data.cod !== 404 ? (
        <React.Fragment>
          <div className="maincard">
            <Card className="weatherCard" style={{ width: "70%" }}>
              <Card.Body>
                <Card.Title>
                  {data.name}, {data.sys.country}
                </Card.Title>
                <Card.Img class="weatherIcon" variant="top" src={iconlink} />
                <Card.Text>{data.weather[0].main} || {Math.floor(data.main.temp - 273.15)}°c</Card.Text>

                <Table className="details-table" striped bordered hover variant="dark">
                  <tr>
                    <td>High / Low</td>
                    <td>
                      {Math.floor(data.main.temp_max - 273.15)}°c /{" "}
                      {Math.floor(data.main.temp_min - 273.15)}°c
                    </td>
                  </tr>
                  <tr>
                    <td>Feels like</td>
                    <td>{Math.floor(data.main.feels_like - 273.15)}°c</td>
                  </tr>
                  <tr>
                    <td>Wind direction</td>
                    <td>{data.wind.deg}° deg</td>
                  </tr>
                  <tr>
                    <td>Wind speed</td>
                    <td>{data.wind.speed} km/h</td>
                  </tr>
                  <tr>
                    <td>Sunrise</td>
                    <td>
                      {new Date(data.sys.sunrise * 1000).toLocaleTimeString()}
                    </td>
                  </tr>
                  <tr>
                    <td>Sunset</td>
                    <td>
                      {new Date(data.sys.sunset * 1000).toLocaleTimeString()}
                    </td>
                  </tr>
                  <tr>
                    <td>Humidity</td>
                    <td>{data.main.humidity}%</td>
                  </tr>
                  <tr>
                    <td>Pressure</td>
                    <td>{data.main.pressure} hPa</td>
                  </tr>
                </Table>
              </Card.Body>
            </Card>
          </div>
        </React.Fragment>
      ) : (
        <div className="maincard">
          <h2>{data.message}</h2>
        </div>
      )}
    </div>
  );
}

export default DisplayWeather;
